<script lang="ts" setup>
import { GoToHome } from '@/commons/Global';
import { CallbackStatus, signinCallback } from '@/commons/oidc/handle';
import { ref } from 'vue';

const [DEFAULT, OK, FAIL] = [
    '正在处理回调...',
    '处理回调成功，正在跳转...',
    '处理回调失败，请查看日志'
]

const state = ref(DEFAULT)

signinCallback()
    .then(code => {
        switch (code) {
            case CallbackStatus.OK:
                state.value = OK
                GoToHome()
                break
            case CallbackStatus.FAILURE:
                state.value = FAIL
                break
        }
    })
    .catch(e => {
        state.value = FAIL
        console.error(e)
    })
</script>

<template>
    <p>{{ state }}</p>
</template>